import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-view-equipment',
  templateUrl: './view-equipment.component.html',
  styleUrls: ['./view-equipment.component.scss']
})
export class ViewEquipmentComponent implements OnInit {
  loginData: any;
  proData: any;
  equipmentListDetails: any;
  equipentListResponse: boolean;
  equipmentDetails: FormGroup;
  equipmentID: string;
  urls = [];
  withImage: boolean;
  withOutImage: boolean;
  imageArray: any = [];
  imageNames: any = [];
  arrayValues: any= [];
  imagePreview: string;
  arr: any = [];
  uniqueArray:  any = [];
  selectedIndex: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    private _Activatedroute:ActivatedRoute,
    ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode;
    this.equipmentID=this._Activatedroute.snapshot.paramMap.get("equipmentId");    
    this.equipmentListOne(this.equipmentID)
    
  }
  equipmentListOne(equipmentID){
    //alert(this.euipmentID)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
  //this.http.get(Global.url + '/equipmentListOne/'  + '/' + euipmentID, options)
    this.http.get(Global.url + '/equipmentListOne' + '/' + equipmentID, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.equipmentListDetails = response.response;

          this.urls = this.equipmentListDetails[0].pictures.split(',')
          //this.urls = this.equipmentListDetails[0].pictures.split(',')
          //console.log("url" + JSON.stringify(this.urls))
          //console.log("urls" + JSON.stringify(this.equipmentListDetails[0].pictures))
          if (this.equipmentListDetails[0].pictures != "") {
            this.withImage = true;
            this.withOutImage = false;
            for (var i = 0; i < this.urls.length; i++) {
              this.imageArray.push(this.urls[i])
              this.imageNames.push({ image: this.urls[i] });
              this.arrayValues.push(this.urls[i])
              this.imagePreview = Global.url + "/" + this.urls[i]
              this.arr.push({ name: this.imagePreview });
            }
            this.uniqueArray = Array.from(new Set(this.arr));
            console.log(" this.uniqueArray" + JSON.stringify(this.uniqueArray))

          }
          console.log("arrarr" + JSON.stringify(this.arr))
          if (this.equipmentListDetails[0].pictures == "" || this.equipmentListDetails[0].pictures == null || this.equipmentListDetails[0].pictures == undefined || this.equipmentListDetails[0].pictures == "undefined") {
            this.withOutImage = true;
            this.withImage = false;
            this.imagePreview = '../../assets/img/no-image.png'
          }

          this.equipentListResponse = true;


        }
        else {
          this.equipentListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  changeColor(i){
    console.log(i);
  this.selectedIndex = i;
  }
  
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  Onedit(){
    this.router.navigate(['/proz/editEquipment',this.equipmentID])
  }
  Oncancel(){
    this.router.navigate(['/proz/listEquipment'])
  }
}
