import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-van',
  templateUrl: './edit-van.component.html',
  styleUrls: ['./edit-van.component.scss']
})
export class EditVanComponent implements OnInit {

  editVan: FormGroup
  loginData: any;
  proData: any;
  getStaffId: any;
  progressShow: boolean = false;
  progressHide: boolean = false;
  img: boolean = false;
  imagePreview: string = "assets/img/no-image.png";
  percentage: any = 0;
  file2Upload: any
  vanData: any;
  dataVan: boolean;
  vanId: any;
  img1: boolean;
  employeeStatus: any;
  WarehouseData: boolean = true;
  VanEnable: boolean = true;
  mailError: boolean;
  disableSignIn: boolean;
  mailResponse: any;
  type: any;
  submitted: boolean;
  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.vanId = this.route.snapshot.params['vanId'];
    this.getStaff()
    this.viewVan()
    this.employeeProSpecific()
    this.editVan = this.formBuilder.group({
      vanId: [null],
      make: [null],
      model: [null],
      year: [null],
      color: [null],
      type: [null],
      description: [null],
      status: [null],
      assignedStaff: [null],
      notes: [null],
      name: [null],
      // staffName: [null],
      images: [null]
    })
  }


  onSubmit() {
    this.submitted = true;
    this.updateVan()
    // stop here if form is invalid
    if (this.editVan.invalid) {

      return;
    }
    else {
    
    }

  }

  get f() { return this.editVan.controls; }
  updateVan() {
    let obj = {
      "proId": this.loginData.loginCode,
      "vanId": this.editVan.value.vanId,
      "model": this.editVan.value.model,
      "make": this.editVan.value.make,
      "year": this.editVan.value.year,
      "color": this.editVan.value.color,
      "type": this.editVan.value.type,
      "description": this.editVan.value.description,
      "assignedStaff": this.editVan.value.assignedStaff,
      "notes": this. editVan.value.notes,
      "status": this.editVan.value.status,
      "name": this.editVan.value.name,
      // "staffName": this.editVan.value.staffName,
      "images": Global.documentName,
      "modifiedOn": new Date(),
      "modifiedBy": this.loginData.emailId,


    }
    console.log("obj" + JSON.stringify(obj))
    this.http.post(Global.url + '/updateVan', obj)
      .subscribe(data => {
        var vanData = data.json();
        console.log("vanData" + JSON.stringify(vanData))
        this.toastr.success('van Added Successfuly...');
        this.router.navigate(['/proz/listLocations'])
      }, error => {
        console.log(error);// Error getting the data
      });

  }
  editData(x) {
    this.editVan = this.formBuilder.group({
      proId: [x.proId, Validators.required],
      vanId: [x.vanId, Validators.required],
      make: [x.make, Validators.required],
      model: [x.model, Validators.required],
      year: [x.year, Validators.required],
      color: [x.color, Validators.required],
      type: [x.type, Validators.required],
      description: [x.description, Validators.required],
      assignedStaff: [x.assignedStaff, Validators.required],
      notes: [x.notes, Validators.required],
      name: [x.name, Validators.required],
      status: [x.status, Validators.required],
      // staffName: [x.staffName, Validators.required],
      images: Global.imagePath,
    })

  }
  getStaff() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getStaffId', options)
      .subscribe(data => {
        var staffId = data.json();
        if (staffId.status == 200) {
          console.log("group Details" + JSON.stringify(staffId))
          this.getStaffId = staffId.response;
          console.log("list" + JSON.stringify(this.getStaffId))
          // return false;
          // this.currentStatus1 = true;
        }
        else {
          // this.currentStatus1 = false;
          this.toastr.warning('Unable to connect to server');
        }
      })
  }

  //IMAGE UPLOAD
  handleImage(file) {
    var picReader = new FileReader();
    picReader.onload = (e: any) => {
      var reader = e.target;
      this.imagePreview = reader.result;
    }
    picReader.readAsDataURL(file)
  }

  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.img = true;
      this.progressShow = false;
      this.progressHide = false;
      this.file2Upload = event.target.files[0];
      this.handleImage(file)
    } else {
      this.img = false;
      this.progressShow = false;
      this.progressHide = false;
      this.imagePreview = '../../assets/img/no-image.png'
    }
  }

  imgfileUpload() {
    console.log("imgfileUpload calling")
    const url = Global.url + "/imageUploadData";
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append("image", this.file2Upload);
    const config = {
      headers: {
        "content-type": "multipart/form-data"
      }
    };
    xhr.open("post", url, true);
    xhr.upload.onprogress = e => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage === 100) {
          this.progressShow = false;
          this.progressHide = true;
          // this.imageUploadVariable.nativeElement.value = "";
          this.img1 = false;
        }
      }
    };
    xhr.onerror = function (e) { };
    xhr.onload = function () {
      var a = JSON.parse(xhr.responseText);
      Global.secimagePath = a.imagePath;
      Global.documentName = a.name;

      console.log("GLObaL NAme" + Global.documentName)
    };
    xhr.send(formData);
  }
  //IMAGE UPLOAD


  mobileValidation() {
    console.log("staffid" + this.editVan.value.staffId)
    var headers = new Headers;
    headers.append('Accept', 'application/json')
    headers.append('Content-Type', 'application/json')
    let options = new RequestOptions({ headers: headers })
    this.http.get(Global.url + '/getStaffRole/' + this.editVan.value.staffId)
      .subscribe(data => {
        var getStaffRole1 = data.json();
        if (getStaffRole1.status == 200) {
          // this.mobileNumberValidation = false;
          console.log('exists' + JSON.stringify(getStaffRole1))
          //let data = getStaffRole1.data
          // this.genAppointment.get('customerName').setValue(mobileNumberValidation.data[0].name)
          this.editVan.get('staffRole').setValue(getStaffRole1.response[0].role)
          this.editVan.get('staffName').setValue(getStaffRole1.response[0].firstName)
        } else {
          // this.mobileNumberValidation = true;
          console.log('does not exists')
        }
      }, error => {
        this.toastr.error('Failed to Connect Server');
      })

  }
  viewVan() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewVan/' + this.vanId, options)
      .subscribe(data => {
        var van = data.json();
        this.vanData = van.response[0];
        this.type = this.vanData.type
        this.TypeSelection(this.type)
        if (this.vanData.images == '') {

          this.imagePreview = "assets/img/no-image.png"
          Global.imagePath = this.vanData.images
          console.log("resume" + this.imagePreview)
        } else {
          this.imagePreview = Global.url + "/" + this.vanData.images
          console.log("resume1" + this.imagePreview)
          Global.imagePath = this.vanData.images
        }
        this.editData(this.vanData)

        console.log("View Van Data" + JSON.stringify(this.vanData))
        this.dataVan = true;

      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  cancelVan() {
    this.router.navigate(['/proz/listLocations'])
  }
  listVan() {
    this.router.navigate(['/proz/listLocations'])
  }

  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  employeeProSpecific() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/VanStaffStatus/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var proStaff = data.json();
        if (proStaff.status == 200) {
          console.log("group Details" + JSON.stringify(proStaff))
          this.employeeStatus = proStaff.response;
          console.log("employeeStatus" + JSON.stringify(this.employeeStatus))
          // return false;
          // this.currentStatus1 = true;
        }
        else {
          // this.currentStatus1 = false;
          this.toastr.warning('Unable to connect to server');
        }
      })
  }

  /*year Validation */
  getToday(): string {
    return new Date().toISOString().split('T')[0]
  }
  /*year Validation */

  TypeSelection(typeData) {
    console.log("TYPE :::" + typeData)
    if (typeData == "Warehouse") {
      this.WarehouseData = false;
      this.VanEnable = false;
    }
    if (typeData == "Van") {
      this.WarehouseData = true;
      this.VanEnable = true;
    }
    if (typeData == "Truck") {
      this.WarehouseData = true;
      this.VanEnable = true;
    }

  }

  subCheckingName() {
    console.log("This is the Name:" + JSON.stringify(this.editVan.value.name))
    if (this.editVan.value.name == "") {
      this.toastr.warning("Name is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkVanName/' + this.editVan.value.name + "/" + this.loginData.loginCode)
        .subscribe(data => {
          this.mailResponse = data.json();
          if (this.mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  
}




