import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-list-equipment',
  templateUrl: './list-equipment.component.html',
  styleUrls: ['./list-equipment.component.scss']
})
export class ListEquipmentComponent implements OnInit {
  loginData: any;
  proData: any;
  equipmentListResponse: boolean;
  equipmentListDetails: any;
  equipmentID: any;

  constructor(public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.getEquipmentList()
    
  }
  getEquipmentList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/equipmentList' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.equipmentListDetails = response.response;
          this.equipmentID = response.response[0].equipmentId;
          //alert(response.response[0].equipmentId)
          this.equipmentListResponse = true;
        }
        else {
          this.equipmentListDetails = null;
          this.equipmentListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  addEquipment(){
    this.router.navigate(['/proz/addNewEquipment'])
  }
  edit(equipmentID){
    this.router.navigate(['/proz/editEquipment',equipmentID])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  Onview(equipmentID){
    this.router.navigate(['/proz/viewEquipment/',equipmentID])
  }
  viewEquipments(equipmentID){
    this.router.navigate(['/proz/viewEquipment',equipmentID])
  }
  book(){
    this.router.navigate(['/proz/bookEquipment'])
  }
}
