import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-list-van',
  templateUrl: './list-van.component.html',
  styleUrls: ['./list-van.component.scss']
})
export class ListVanComponent implements OnInit {
  loginData: any;
  vanList: any;
  employeeList: any;
  showEmployeeData: boolean;
  vanId: boolean;
  make: boolean;
  model: boolean;
  year: boolean;
  type: boolean;
  Status: boolean;
  All: boolean;
  searchVan: FormGroup;
  staffName: any;
  getVanId: any;
  selectEmployeeId: string;
  displayFileModalEquipStatus: string;
  vans: any;
  searchLocation: FormGroup;
  locationData: any;
  searchShow: boolean;
  showList: boolean;

  constructor(public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    console.log("loginData" + JSON.stringify(this.loginData.loginCode))
    this.listVans()
    this.listEmployeeDetails()
    this.searchLocation = this.formBuilder.group({
      vanId: [null],
      model: [null],
      make: [null],
      year: [null],
      type: [null],
      status: [null],
      selectBy: [null]
    })
  }
  
  listVans() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listVan/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("group Details" + JSON.stringify(response))
          this.vanList = response.response;
          console.log("list" + JSON.stringify(this.vanList))
          this.showList=true;
         } else {
          this.vanList=[]
          this.showList = false;
        }
      })
  }
  editStaff(id){
    this.router.navigate(['/proz/editLocation', id])
  }
  viewStaff(id){
    this.router.navigate(['/proz/viewLoation', id])
  }
  // Add Van
  addVan(){
    this.router.navigate(['/proz/addLocatin'])
  }
    // Add Van

    // get staff details
  listEmployeeDetails() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/employeeList/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("employee Details" + JSON.stringify(response))
          this.employeeList = response.response;
          console.log("list" + JSON.stringify(this.employeeList))
          this.showEmployeeData = true;
          return false;
        } else {
          this.showEmployeeData = false;
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
    // get staff details

  vanTypeSelection(x) {
    console.log("vanTypeSelection" + x)
    if (x == "vanId") {
      this.vanId = true;
      this.make = false;
      this.model = false;
      this.year = false;
      this.type = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "make") {
      this.vanId = false;
      this.make = true;
      this.model = false;
      this.year = false;
      this.type = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "model") {
      this.vanId = false;
      this.make = false;
      this.model = true;
      this.year = false;
      this.type = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "year") {
      this.vanId = false;
      this.make = false;
      this.model = false;
      this.year = true;
      this.type = false;
      this.Status = false;
      this.All = false;
    }
    if (x == "type") {
      this.vanId = false;
      this.make = false;
      this.model = false;
      this.year = false;
      this.type = true;
      this.Status = false;
      this.All = false;
    }
    if (x == "Status") {
      this.vanId = false;
      this.make = false;
      this.model = false;
      this.year = false;
      this.type = false;
      this.All = false;
      this.Status = true;
    }
  }
  // saveSelectedInventory(){
  //   console.log("Selected Radio Btton" + this.selectEmployeeId)
  //   this.mobileValidation()
  // }
  vanDetails(vanId){
    console.log("Van Id" + vanId)
    this.getVanId = vanId
  }
  saveSelectedInventory(){
    console.log("staffid" + this.selectEmployeeId)
    var headers = new Headers;
    headers.append('Accept', 'application/json')
    headers.append('Content-Type', 'application/json')
    let options = new RequestOptions({ headers: headers })
    this.http.get(Global.url + '/getStaffRole/' + this.selectEmployeeId)
      .subscribe(data => {
        var getStaffRole1 = data.json();
        if (getStaffRole1.status == 200) {
          console.log("staffName"+getStaffRole1.response[0].firstName)
          this.staffName = getStaffRole1.response[0].firstName        
          // console.log('exists' + JSON.stringify(getStaffRole1))
          this.updateVanData(this.staffName)
        } else {
          // this.mobileNumberValidation = true;
          console.log('does not exists')
        }
      }, error => {
        this.toastr.error('Failed to Connect Server');
      })
  }
  updateVanData(staffName){
    let obj={
      "vanId": "" + this.getVanId,
      "staffId": "" + this.selectEmployeeId,
      "staffName": "" + staffName,
    }
    this.http.post(Global.url + '/updateVanDetails', obj)
      .subscribe(data => {
        var updateVanDetails = data.json();
        if (updateVanDetails.status == 200) {
          this.listVans()
        } else {
          // this.mobileNumberValidation = true;
          console.log('does not exists')
        }
      }, error => {
        this.toastr.error('Failed to Connect Server');
      })
  }
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list() {
    this.router.navigate(['/inventory/listInventory'])
  }

  /* Delete Vans*/
  deleteVan(data) {
    // var id = id;
    this.displayFileModalEquipStatus = "block";
    this.vanId = data.vanId;
    this.type = data.type;
    console.log("id.." + this.vanId);
  }
  onDeleteEquipmentStatus() {
    console.log("equipmentStatus is delete" + this.vanId);
    event.preventDefault();
    event.stopPropagation();
    this.http
      .get(Global.url + "/deleteVan/" + this.vanId)
      .subscribe((data: any) => {
        this.vans = data;
        if (this.vans.status == 200) {
          this.displayFileModalEquipStatus = "none";
          this.toastr.success("Location deleted Successfully ");
          this.listVans();
        } else {
          console.log("The else");
        }
      });
  }
  onCloseHandledEquipmentStatus() {
    this.displayFileModalEquipStatus = "none";
  }
  /* Delete Vans*/
  reset(){
    this.searchLocation.reset();
    this.listVans()
  }
  searchFunctionality() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    let obj = {
      "vanId": this.searchLocation.value.vanId,
      "type": this.searchLocation.value.type,
      "make": this.searchLocation.value.make,
      "model": this.searchLocation.value.model,
      "year": this.searchLocation.value.year,
      "status": this.searchLocation.value.status,
      "selectBy": this.searchLocation.value.selectBy,
    }
    console.log(obj)
    if (this.searchLocation.value.selectBy != 'All'){
    this.http.post(Global.url + '/searchLocations/' + this.loginData.loginCode, [
      { id: 'vanId', value: obj.vanId },
      { id: 'type', value: obj.type },
      { id: 'make', value: obj.make },
      { id: 'model', value: obj.model },
      { id: 'year', value: obj.year },
      { id: 'status', value: obj.status },
      //{ id: 'selectBy', value: obj.selectBy }
    ], options).subscribe((data: any) => {
      this.locationData = data.json();
      //console.log("Inventory Search" + JSON.stringify(this.inventorySearch))
      if (this.locationData.status == 200) {
        this.vanList = this.locationData.response;
        //console.log("searchInventoryData values" + JSON.stringify(this.inventoryListDetails))
        this.showList = true;
        this.searchLocation.reset();
      } else {
        this.showList = false;
        this.vanList = [];
      }
  
    })
    }
    else{
      this.listVans()
    }
  }
}
