import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-view-equipmentbooking',
  templateUrl: './view-equipmentbooking.component.html',
  styleUrls: ['./view-equipmentbooking.component.scss']
})
export class ViewEquipmentbookingComponent implements OnInit {
  loginData: any;
  proData: any;
  equipmentBookId: string;
  equipmentBookingListDetails: any;
  equipentBookingListResponse: boolean;

  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    private _Activatedroute:ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode;
    this.equipmentBookId=this._Activatedroute.snapshot.paramMap.get("equipmentBookId");    
    this.equipmentBookingListOne(this.equipmentBookId)
  }
  equipmentBookingListOne(equipmentBookId){
    //alert(this.euipmentID)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
  //this.http.get(Global.url + '/equipmentListOne/'  + '/' + euipmentID, options)
    this.http.get(Global.url + '/equipmentBookingListOne' + '/' + equipmentBookId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.equipmentBookingListDetails = response.response;
          this.equipentBookingListResponse = true;
        }
        else {
          this.equipentBookingListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  Onedit(equipmentBookId){
    this.router.navigate(['/proz/editEquipmentBooking',equipmentBookId])
  }
  Oncancel(){
    this.router.navigate(['/proz/listEquipmentBooking'])
  }
}
