import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../global/global';
import { Utility } from '../../../../global/config';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-edit-equipment',
  templateUrl: './edit-equipment.component.html',
  styleUrls: ['./edit-equipment.component.scss']
})
export class EditEquipmentComponent implements OnInit {
  loginData: any;
  proData: any;
  equipmentID: any;
  equipmentListDetails: any;
  editEquipment: FormGroup;
  equipmentDataStatus:boolean = false;
  equipmentCat: any;
  priceBookServiceResponse: boolean;
  equipmentsubCat: any;
  vendorListDetails: any;
  vendorListResponse: boolean;
  submitted: boolean = false;
  statesData: any;
  urls = [];
  withOutImage: boolean;
  withImage: boolean;
  imageArray: any = [];
  filesToUpload: Array<File> = [];
  formData: any;
  newArray: any = [];
  uniqueArray:  any = [];
  imageNames: any = [];
  arrayValues: any;  
  arr: any[];
  percentage: number = 0;
  progressShow: boolean;
  img: boolean;
  progressHide: boolean;
  file2Upload: any;
  progressHide1: boolean;
  chosseTypecount: number;
  fileName: string;
  imageuploadstatus: boolean;
  value: any;
  imagePreview: string = "../../../assets/img/no-image.png"

  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    private _Activatedroute:ActivatedRoute,
    private formBuilder: FormBuilder,
    public toastr: ToastrService,
    ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.equipmentID=this._Activatedroute.snapshot.paramMap.get("equipmentId");    
    this.equipmentListOne(this.equipmentID)
    this.getEquipmentCategory();
    this.getVendorList();
    this.getListOfStates()
    this.editEquipment = this.formBuilder.group({
      equipmentId: ['', Validators.required],  
      description: ['', Validators.required],
      equipmentName: ['', Validators.required],
      date: [`${Utility.year}-${Utility.month}-${Utility.day}`, Validators.required],
      showOnEquipmentBoard: [false],
      itemCategoryCode: ['', Validators.required],
      itemSubCategoryCode: ['', Validators.required],
      location: ['', Validators.required], 
      size: ['', Validators.required],
      color: ['', Validators.required],
      model: ['', Validators.required],
      serialNo: ['', Validators.required],
      manufacturer: ['', Validators.required],
      vendor: [''],     
      PurchasePrice: ['', Validators.required],
      status: ['', Validators.required],
      presentPrice: ['', Validators.required],
    })
  }
  equipmentListOne(euipmentID){
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/equipmentListOne' + '/' + euipmentID, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.equipmentListDetails = response.response;
          this.editEquipment = this.formBuilder.group({
            equipmentId: [this.equipmentListDetails[0].equipmentId, Validators.required],  
            description: [this.equipmentListDetails[0].description, Validators.required],
            equipmentName: [this.equipmentListDetails[0].equipmentName, Validators.required],
            date: [this.equipmentListDetails[0].date, Validators.required],
            showOnEquipmentBoard: [this.equipmentListDetails[0].showOnEquipmentBoard],
            itemCategoryCode: [this.equipmentListDetails[0].category, Validators.required],
            itemSubCategoryCode: [this.equipmentListDetails[0].subCategory, Validators.required],
            location: [this.equipmentListDetails[0].location, Validators.required], 
            size: [this.equipmentListDetails[0].size],
            color: [this.equipmentListDetails[0].color],
            model: [this.equipmentListDetails[0].model],
            serialNo: [this.equipmentListDetails[0].serialNo],
            manufacturer: [this.equipmentListDetails[0].manufacturer],
            vendor: [this.equipmentListDetails[0].vendorName],     
            PurchasePrice: [this.equipmentListDetails[0].purchasePrice, Validators.required],
            status: [this.equipmentListDetails[0].status, Validators.required],
            presentPrice: [this.equipmentListDetails[0].presentPrice, Validators.required],            
          })
          this.getEquipmentSubCategory(this.equipmentListDetails[0].category);
           this.urls = this.equipmentListDetails[0].pictures.split(',')
           console.log("url" + JSON.stringify(this.urls))
           this.urls = this.equipmentListDetails[0].pictures.split(',')
           //this.urls = this.equipmentListDetails[0].pictures.split(',')
           //console.log("url" + JSON.stringify(this.urls))
           //console.log("urls" + JSON.stringify(this.equipmentListDetails[0].pictures))
           if (this.equipmentListDetails[0].pictures != "") {
             this.withImage = true;
             this.withOutImage = false;
             for (var i = 0; i < this.urls.length; i++) {
               this.imageArray.push(this.urls[i])
               this.imageNames.push({ image: this.urls[i] });
               this.arrayValues.push(this.urls[i])
               this.imagePreview = Global.url + "/" + this.urls[i]
               this.arr.push({ name: this.imagePreview });
             }
             this.uniqueArray = Array.from(new Set(this.arr));
             console.log(" this.uniqueArray" + JSON.stringify(this.uniqueArray))
 
           }
           console.log("arrarr" + JSON.stringify(this.arr))
           if (this.equipmentListDetails[0].pictures == "" || this.equipmentListDetails[0].pictures == null || this.equipmentListDetails[0].pictures == undefined || this.equipmentListDetails[0].pictures == "undefined") {
             this.withOutImage = true;
             this.withImage = false;
             this.imagePreview = '../../assets/img/no-image.png'
           }
          
         // this.equipentListResponse = true;
        }else {
          //this.equipentListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  handleImage(file) {
    var picReader = new FileReader();
    picReader.onload = (e: any) => {
      var reader = e.target;
      this.imagePreview = reader.result;
    }
    picReader.readAsDataURL(file)
  }
  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.img = true;
      this.progressShow = false;
      this.progressHide = false;
      this.file2Upload = event.target.files[0];
      this.handleImage(file)
    } else {
      this.img = false;
      this.progressShow = false;
      this.progressHide = false;
      this.imagePreview = "../../../assets/img/no-image.png"
    }
  }
  
    onSelectFile(fileInput) {
      this.arr = []
      this.imageArray = []
      this.progressHide = false
      console.log("fileInput.target.files" + fileInput.target.files)
      console.log("Array checking" + fileInput.target.files.length)
      if (fileInput.target.files.length == 1) {
        this.filesToUpload.push(fileInput.target.files[0])
        this.img = true;
        this.withOutImage = true
        this.chosseTypecount = this.filesToUpload.length
      } else {
        for (var i = 0; i < fileInput.target.files.length; i++) {
          this.img = true;
          this.withOutImage = true
          this.filesToUpload.push(fileInput.target.files[i])
          console.log("this.filesTOUPLOAD" + JSON.stringify(this.filesToUpload.length))
          this.chosseTypecount = this.filesToUpload.length
          this.fileName = "files"
          console.log("this.chosseTypecount" + this.fileName)
        }
      }
      var filesAmount = fileInput.target.files.length;
      console.log("filesAmount" + filesAmount)
      for (let i = 0; i < filesAmount; i++) {
        var reader = new FileReader();
        this.img = true;
        this.withOutImage = true
        reader.onload = (fileInput: any) => {
          console.log(fileInput.target.result);
          this.urls.push(fileInput.target.result);
          // this.arr.push({ name: fileInput.target.result });
          // console.log("checking data" + this.urls.length)
          // console.log("array names" + JSON.stringify(this.arr))
  
          this.imageuploadstatus = true;
  
        }
  
        reader.readAsDataURL(fileInput.target.files[i]);
      }
      this.uploadData()
    }
  
    uploadData() {
      console.log("upload is calling")
      const files: Array<File> = this.filesToUpload;
      //console.log(files);
      console.log("files.lenght" + files.length)
  
      this.formData = new FormData();
      for (let i = 0; i < files.length; i++) {
        this.formData.append("fileImage", files[i]);
      }
      this.http.post(Global.url + '/multiEqiImgData', this.formData)
        .map(files => files.json())
        .subscribe(files => {
          for (var i = 0; i < files.imagePath.length; i++) {
            this.newArray.push(files.imagePath[i])
            console.log("  this.newArray" + JSON.stringify(this.newArray))
          }
          this.uniqueArray = Array.from(new Set(this.newArray));
          this.imageNames = Object.keys(this.uniqueArray).map(key => ({ type: key, image: this.uniqueArray[key] }));
          this.arr = Object.keys(this.uniqueArray).map(key => ({ type: key, name: Global.url + "/" + this.uniqueArray[key] }));
        }
        )
  
  
    }
    progressData() {
      const url = Global.url + "/multiEqiImgData";
      const formData = new FormData();
      var xhr = new XMLHttpRequest();
      formData.append("fileImage", this.file2Upload);
      const config = {
        headers: {
          "content-type": "multipart/form-data"
        }
      };
      xhr.open("post", url, true);
      xhr.upload.onprogress = e => {
        if (e.lengthComputable) {
          this.percentage = (e.loaded / e.total) * 100;
          this.progressShow = true;
          this.progressHide = false;
          if (this.percentage === 100) {
            this.progressShow = false;
            this.progressHide = true;
  
          }
        }
      };
      xhr.onerror = function (e) { };
      xhr.onload = function () {
        var a = JSON.parse(xhr.responseText);
        Global.secimagePath = a.imagePath;
      };
      xhr.send(formData);
    }
  
    removeSelectedFile(i) {
      this.arr.splice(i, 1);
      this.imageNames.splice(i, 1);
      this.uniqueArray = []
      for (var p = 0; p < this.arr.length; p++) {
        this.value = this.arr[p].name
        this.uniqueArray.push(this.arr[p].name.substring(25, 50))
        console.log('uniqueArray'+''+this.uniqueArray)
      }
    }
  getToday (){

  }
  getListOfStates() {
    // this.zipcodeValue = JSON.parse(localStorage.getItem('zipcode'))
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  getVendorList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfVendors' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.vendorListDetails = response.response;
          this.vendorListResponse = true;
        }
        else {
          this.vendorListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  get f() { return this.editEquipment.controls; }
  getEquipmentCategory() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/equipmentProcat' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.equipmentCat = response.response;

          console.log("equipmentCat details" + JSON.stringify(this.equipmentCat))

          this.priceBookServiceResponse = true;
        }
        else {
          this.priceBookServiceResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  getEquipmentSubCategory(category) {
    console.log("category" + category)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/equipmentProSubcatgory/' + category + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.equipmentsubCat = response.response;

          console.log("equipmentCat details" + JSON.stringify(this.equipmentCat))

          this.priceBookServiceResponse = true;
        }
        else {
          this.priceBookServiceResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list(){
    this.router.navigate(['/proz/listEquipment'])
  }
  Onupdate(){
    this.router.navigate(['/proz/listEquipment']) 
  }

  Oncancel(){
    this.router.navigate(['/proz/listEquipment'])
  }
  onSubmit() {
    this.submitted = true;
    if (this.editEquipment.value.equipmentID == '') {
      return
    }
    else if (this.editEquipment.value.equipmentName == '') {
      return
    }
    else if (this.editEquipment.value.status == '') {
      return
    }

    else {
      this.saveButton()
    }

  }
  saveButton() {
    if (this.editEquipment.value.itemCategoryCode != '' && this.editEquipment.value.itemSubCategoryCode != '' && this.editEquipment.value.name != '') {
      let addObj = {
        "proId": this.loginData.loginCode,
        "equipmentId": '' + this.equipmentID,//this.editEquipment.value.equipmentId,        
        "equipmentName": '' + this.editEquipment.value.equipmentName,
        "description": '' + this.editEquipment.value.description,
        "date": '' + this.editEquipment.value.date,
        //"existingQty": '' + this.existQty == undefined ? '' : this.existQty,
        "location": '' + this.editEquipment.value.location == undefined ? '' : this.editEquipment.value.location,
        //"qty": '' + this.quantityAdd == undefined ? '' : this.quantityAdd,
        
        //"totalQuantity": '' + this.totalQuantityData == undefined ? '' : this.totalQuantityData,
        "manufacturer": '' + this.editEquipment.value.manufacturer,
        "model": '' + this.editEquipment.value.model,
        "size": '' + this.editEquipment.value.size,
        "color": '' + this.editEquipment.value.color,
        "showOnEquipmentBoard": '' + this.editEquipment.value.showOnEquipmentBoard,
        "category": '' + this.editEquipment.value.itemCategoryCode == undefined ? '' : this.editEquipment.value.itemCategoryCode,
        "subCategory": '' + this.editEquipment.value.itemSubCategoryCode == undefined ? '' : this.editEquipment.value.itemSubCategoryCode,
        "serialNo": '' + this.editEquipment.value.serialNo,
        "purchasePrice": '' + this.editEquipment.value.PurchasePrice,
        //"vendorName": '' + this.editEquipment.value.vendorName,
       // "webLinkDetails": '' + this.editEquipment.value.webLinkDetails,
        "vendorName": '' + this.editEquipment.value.vendor == undefined?'':this.editEquipment.value.vendor,
        //"type": '' + this.editEquipment.value.type,
        //"unit": '' + this.editEquipment.value.unit == undefined ? '' : this.editEquipment.value.unit,
        "presentPrice": '' + this.editEquipment.value.presentPrice,
        "status": '' + this.editEquipment.value.status, 
        
        "pictures": '' + this.uniqueArray,
        
      }
      console.log("ADD OBJ" + JSON.stringify(addObj))
      this.http.post(Global.url + '/editEquipment' , addObj)
        .subscribe(data => {
          var partInfoData = data.json();
          if (partInfoData.status == 200) {
           // alert(200)
            this.toastr.success('Equipment updated Successfuly...');
            //this.listEquipment();
           this.router.navigate(['/proz/listEquipment'])

          } else {
          }
        }, error => {
          console.log(error);// Error getting the data
        });
    }
  }
}
