import { Component, OnInit } from '@angular/core';
import { Radio } from '../../../utilities/radiobutton'
import { Beyond } from '../../../utilities/beyond';
import { FormGroup } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  providers: [Radio, Beyond],
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss']
})
export class FeedbackComponent implements OnInit {

  constructor(
    public beyond: Beyond,
    public radio: Radio,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.radio.radiobutton()
    this.beyond.beyondAdmin()

  }
  skipButton() {
    this.router.navigate(['/invoice/viewInvoice'])
  }
  continue() {
    this.toastr.success('Thanks for your valuable feedback');
    this.router.navigate(['/invoice/viewInvoice'])
  }
}
