import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProzLayoutComponent } from '../proz/proz-layout/proz-layout.component';
// STAFF COMPONENTS START

// LIST ROLES
// LIST ROLES
// STAFF ACCOUNT SETTINGS

import { AddVanComponent } from './van/add-van/add-van.component';
import { ListVanComponent } from './van/list-van/list-van.component';
import { ViewVanComponent } from './van/view-van/view-van.component';
import { EditVanComponent } from './van/edit-van/edit-van.component';
/* TWILLIO */
import { TwiliosmslogComponent } from './proz-twilio-log/twiliosmslog/twiliosmslog.component';
import { TwiliocalllogComponent } from './proz-twilio-log/twiliocalllog/twiliocalllog.component';
/* /TWILLIO */
import { FeedbackComponent } from './feedback/feedback.component';
import { TermsConditionsComponent } from './terms-conditions/terms-conditions.component';

import { ListEquipmentComponent } from './Equipment/list-equipment/list-equipment.component';
import { ViewEquipmentComponent } from './Equipment/view-equipment/view-equipment.component';
import { EditEquipmentComponent } from './Equipment/edit-equipment/edit-equipment.component';
import { AddNewEquipmentComponent } from './Equipment/add-new-equipment/add-new-equipment.component';
import { BookEquipmentComponent } from './Equipment/book-equipment/book-equipment.component';
import { EquipmentbookingBoardComponent } from './Equipment/equipmentbooking-board/equipmentbooking-board.component';
import { EquipmentbookingListComponent } from './equipment/equipmentbooking-list/equipmentbooking-list.component';
import { ViewEquipmentbookingComponent } from './equipment/view-equipmentbooking/view-equipmentbooking.component';
import { EditEquipmentbookingComponent } from './equipment/edit-equipmentbooking/edit-equipmentbooking.component';

const routes: Routes = [
  {
    path: '', component: ProzLayoutComponent,
    children: [



      // {
      //   path: 'addProTimeActivity',
      //   component: AddProTimeActivityComponent
      // },
      // {
      //   path: 'editProTimeActivity',
      //   component: EditProTimeActivityComponent
      // },
      // {
      //   path: 'addProEmployeeStatus',
      //   component: AddProEmployeeStatusComponent
      // },
      // {
      //   path: 'addProCurrentStatus',
      //   component: AddProCurrentStatusComponent
      // },
      // {
      //   path: 'addProPaymentMethod',
      //   component: AddProPaymentMethodComponent
      // },
      // {
      //   path: 'addProGroup',
      //   component: AddProGroupComponent
      // },
      // {
      //   path: 'addProStaffRolePermission',
      //   component: AddProStaffRolePermissionsComponent
      // },

      /* -- Van -- */
      {
        path: 'addLocatin',
        component: AddVanComponent
      },
      {
        path: 'listLocations',
        component: ListVanComponent
      },
      {
        path: 'viewLoation/:vanId',
        component: ViewVanComponent
      },
      {
        path: 'editLocation/:vanId',
        component: EditVanComponent
      },


      /* -- /Van -- */

      /* -- Twillio -- */
      {
        path: 'smslog',
        component: TwiliosmslogComponent
      },
      {
        path: 'calllog',
        component: TwiliocalllogComponent
      },
      /* -- /Twillio -- */

      /* -- Invoices -- */

      {
        path: 'feedback',
        component: FeedbackComponent
      },
      {
        path: 'terms/:proId',
        component: TermsConditionsComponent
      },
      /* -- Equipment -- */
      {
        path: 'addNewEquipment',
        component: AddNewEquipmentComponent
      },
      {
        path: 'listEquipment',
        component: ListEquipmentComponent
      },
      {
        path: 'viewEquipment/:equipmentId',
        component: ViewEquipmentComponent
      },
      {
        path: 'editEquipment/:equipmentId',
        component: EditEquipmentComponent
      },
      {
        path: 'bookEquipment',
        component: BookEquipmentComponent
      },
      {
        path: 'bookinboardEquipment',
        component: EquipmentbookingBoardComponent
      },
      {
        path: 'listEquipmentBooking',
        component: EquipmentbookingListComponent
      },
      {
        path: 'editEquipmentBooking/:equipmentBookId',
        component: EditEquipmentbookingComponent
      },
      {
        path: 'viewEquipmentBooking/:equipmentBookId',
        component: ViewEquipmentbookingComponent
      },
      
      /* -- /Equipment -- */

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzRoutingModule { }
