import { Component, OnInit } from '@angular/core';
import {Beyond} from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
@Component({
  providers:[Beyond],
  selector: 'app-twiliosmslog',
  templateUrl: './twiliosmslog.component.html',
  styleUrls: ['./twiliosmslog.component.scss']
})
export class TwiliosmslogComponent implements OnInit {

  logData:any;
  loginData:any;
  showData:boolean=false;
  constructor(
    public beyond:Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
  ) {}

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    console.log("loginData" + JSON.stringify(this.loginData.loginCode))
    this.listProSubscriptions(this.loginData .loginCode)
  }
  listProSubscriptions(proCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getSmslog/' + proCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("employee Details"+JSON.stringify(response))
          this.logData = response.response;
          this.showData=true
          this.logData.forEach((e,i) => {
              e.row=i+1
          });
        }
      }, error => {
        console.log("Unable to connect to server");
      })
  }
}
