employeeStatus(status) {
  switch (status) {
      case 0: status = "Booked"; break;
      case 1: status = "InUse"; break;
      case 2: status = "Returned"; break;
      case 4: status = "Cancel"; break;
      case 5: status = "Unavailable"; break;
      case 6: status = "In-Repair"; break;
  }
  return status;
}

ejGridDataBound(args: any) {
  for (var i = 0; i < this.gridObj.columns.length; i++) {
      //(this.gridObj.columns[0] as any).width = 10;
      if ((this.gridObj.columns[i] as any).field === "createdOn") {
          (this.gridObj.columns[i] as any).type = "date";
      }
      if ((this.gridObj.columns[i] as any).type === "date") {
          (this.gridObj.columns[i] as any).format = { type: "dateTime", format: "dd/MM/yyyy hh:mm a" };
      }
      (this.gridObj.columns[2] as any).format = "P2";
  }
  this.gridObj.refreshColumns();
}
getProStaffName() {
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let options = new RequestOptions({ headers: headers });
  let url = Global.url + '/equipmentList/' + this.loginData.loginCode;
  if (this.filterGroup) {
      url = url + '?group=' + this.filterGroup
  }
  this.http.get(url, options)
      .subscribe(data => {
          var response = data.json();
          if (response.status == 200) {
              this.proStaffData = response.response;
              //alert(response.response[0].equipmentName)
              //dummy user
              // this.proStaffData.push({"Id":184,"employeeId":"STAFF-0034","Text":"Auqib","lastName":"Ghosal","mobilePhone":"787-897-9797","skillLevel":"1","GroupId":"Test","Designation":"Dispatcher","showOnDispatchboard":"true","employeStatus":"ACTIVE","prozId":"PRO1076","image":"","attendanceStatus":1,"email":"subhasis.ghosal@gmail.com","clockedIn":"Wed Aug 12 2020 22:00:00","clockedOut":"Wed Aug 12 2020 22:00:00","userName":"subhasis.ghosal@gmail.com","fromTime":"06:00 AM","toTime":"08:00 PM","latitude":null,"longitude":null,"lastUpdatedTime":"Wed Aug 12 2020 22:00:00","flag":2,"woNumber":null,"clocked_in":"Wed Aug 12 2020 22:00:00","clocked_out":"Wed Aug 12 2020 22:00:00","Count":0})
              this.staffDataSource = this.proStaffData;
              this.consultantDataSource = this.proStaffData.filter(e=>e['showOnEquipmentBoard']=='true');
          } else {
              this.proStaffData = [];
              this.consultantDataSource = [];
          }
      }, error => {
          this.toastr.warning('Unable to connect to server');
      })
}
refresh() {
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let options = new RequestOptions({ headers: headers });
  this.refreshIntervalSubscriber = interval(5 * 60 * 1000).pipe(
      switchMap(() => {
          let url = Global.url + '/equipmentList/' + this.loginData.loginCode;
          if (this.filterGroup) {
              url = url + '?group=' + this.filterGroup
          }
          return this.http.get(url, {
              headers
          })
      }
      ),
      map(res => res.json())
  ).subscribe((response: any) => {
      if (response.status == 200) {
          this.consultantDataSource = response.response;
      } else {
          this.proStaffData = [];
          this.consultantDataSource = [];
      }
  });
}


