showConfirmPopup(checkbox, event) {
  let closeButtonClick = true
  this.ejResourceDialog.zIndex = 0;
  let sd = this.scheduleObj.selectedDate
  let equipmentName = ` ${this.selectedResourceData.equipmentName}`
  let todayWO = this.workOrderDetailsData.filter(f => {
      let wod = new Date(f.StartTime)
      return new Date(f.EndTime)>=wod && f.ConsultantID == event.resourceData.Id && sd.getFullYear() == wod.getFullYear() && sd.getMonth() == wod.getMonth() && sd.getDay() == wod.getDay()
  })
  console.log("wwwwwwwwwwww", checkbox, todayWO)
  // if(this.selectedResourceData.attendanceStatus == 4 || this.selectedResourceData.attendanceStatus ==5 ){
  if (!checkbox.checked) {
      //this.updateEmployeeStatus(checkbox)
  } else {
      let message = `Are you sure you want to change ${equipmentName} status to ${checkbox.id == 'sick' ? 'Sick' : 'Off'}`;
      this.UtilComponent = DialogUtility.confirm({
          title: '',
          content: `${message}`,
          okButton: {
              text: 'YES', click: () => {
                  this.UtilComponent.hide()
                  closeButtonClick = false
                  if (todayWO && todayWO.length) {
                      this.ejResourceDialog.hide()
                      DialogUtility.alert(`${equipmentName} has assigned Workorders, please reassign or cancel Assigned WOs to proceed`);
                  } else {
                      //this.updateEmployeeStatus(checkbox)
                  }
              }
          },
          cancelButton: { text: 'NO', click: () => { this.UtilComponent.hide(); checkbox.checked = false; } },
          showCloseIcon: true,
          close: (e) => {
              console.log("close", closeButtonClick)
              this.UtilComponent.hide();
              if (closeButtonClick) {
                  checkbox.checked = false;
              }
              this.ejResourceDialog.zIndex = 1000;
          },
          closeOnEscape: true,
          animationSettings: { effect: 'Zoom' },
          position: { Y: 'top' }
      });
  }
}


