getEqBookingDetails() {
  //alert('Abc'+ this.selectedDate.toDateString)
  var headers = new Headers();
  headers.append("Accept", 'application/json');
  headers.append('Content-Type', 'application/json');
  let options = new RequestOptions({ headers: headers });
  let schedulerDate =  `2020-10-16`;//this.selectedDate;
  
  this.http.get(Global.url + '/getEquipMentBookingDetails?schedulerDate=' + schedulerDate, options)
      .subscribe(data => {
          var response = data.json();
          if (response.status == 200) {
            
              this.equipmentBookingDetailsData = response.response.filter(wo => wo.status == "Booked");
             // alert('Abc'+ JSON.stringify(this.equipmentBookingDetailsData))
              for (let i = 0; i < this.equipmentBookingDetailsData.length; i++) {

                  this.equipmentBookingDetailsData[i].equipmentBookId = (this.equipmentBookingDetailsData[i].equipmentBookId);
                  this.equipmentBookingDetailsData[i].address = this.equipmentBookingDetailsData[i].address.replace(/,/g, "");
                  this.equipmentBookingDetailsData[i].checkOutDate = new Date(this.equipmentBookingDetailsData[i].checkOutDate).toDateString() + " " + new Date(this.equipmentBookingDetailsData[i].checkOutDate).toLocaleString('en-US', {
                      hour: 'numeric',
                      minute: 'numeric', hour12: true
                  })
              }
              alert('Abc'+ JSON.stringify(this.equipmentBookingDetailsData))
              this.scheduleObj.eventSettings.dataSource = this.equipmentBookingDetailsData;
              this.scheduleObj.refreshEvents();
          } else {
            alert('No')
              this.scheduleObj.eventSettings.dataSource = [];
          }
      }, (error) => {
          this.toastr.warning('Unable to connect to server');
      })
}

